body{
    font-family: 'Comfortaa', Arial, sans-serif;
    background-color: rgb(245, 239, 233);
    
} 

 .cards {
    background-color: rgb(245, 239, 233);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    
} 
 .product-card {
    padding: 6px;
    
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin: 1rem 1em;
   
    
    height: 25rem;
    display:-webkit-flex;
    display:flex;
    -webkit-flex: 0 1 20%;
            flex: 0 1 20%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    
   background-color: white;;

} 

.product-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.4);
  }

.product-img{
    width: 15em;
}
button {
    border: none;
     background-color: white;
}
.delete-btn{
    border: 1px solid gray;
}


@media screen and (max-width: 40em) {
    .cards {
        display: -webkit-flex;
        display: flex;
       -webkit-justify-content: space-around;
               justify-content: space-around;
       
   }
 
    .product-card {
        -webkit-flex-grow: 0;
                flex-grow: 0;
        -webkit-flex-shrink: 1;
                flex-shrink: 1;
        -webkit-flex-basis: calc(95% - 1em);
                flex-basis: calc(95% - 1em);
    }
}
@media screen and (min-width: 40em) {
    .cards {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        
   }
 
    .product-card {
        -webkit-flex-grow: 0;
                flex-grow: 0;
        -webkit-flex-shrink: 1;
                flex-shrink: 1;
        -webkit-flex-basis: calc(45% - 1em);
                flex-basis: calc(45% - 1em);
    }
}
 
 
@media screen and (min-width: 60em) {
    .cards {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        margin: 1rem .25em;
   }
     
    .product-card {
        -webkit-flex-grow: 0;
                flex-grow: 0;
        -webkit-flex-shrink: 1;
                flex-shrink: 1;
        -webkit-flex-basis: calc(23% - 1em);
                flex-basis: calc(23% - 1em);
        
     }
}
 


